@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.aw-loader:before {
  height: 24px;
  width: 24px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -12px;
  margin-top: -12px;
  -webkit-animation: spin 0.8s linear infinite;
          animation: spin 0.8s linear infinite;
  content: "";
  background: url("../img/loader.svg") center center;
  background-size: cover;
  line-height: 1;
  text-align: center;
  font-size: 2em;
  color: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.aw-loader--left:before {
  left: 0;
  margin-left: 0;
}

#wpadminbar,
#adminmenuwrap,
#adminmenuback,
#adminmenu,
#wpfooter {
  display: none !important;
}

html.wp-toolbar {
  padding: 0 !important;
}

#wpcontent {
  padding: 0 !important;
  margin: 0 !important;
}

.aw-preview {
  position: relative;
  height: 100%;
  overflow: hidden;
}
.aw-preview iframe {
  height: 100%;
}
.aw-preview__header {
  position: relative;
  z-index: 5;
  padding: 19px 50px 24px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.05) 0 1px 2px 1px;
          box-shadow: rgba(0, 0, 0, 0.05) 0 1px 2px 1px;
  border-bottom: 1px solid #E4E4E4;
  background: #ffffff;
}
.aw-preview__header-left {
  font-size: 13px;
  color: #787878;
  display: inline-block;
}
.aw-preview__header-right {
  margin: 10px 0 0;
}
@media (min-width: 800px) {
  .aw-preview__header-right {
    margin: 4px 0 0;
    float: right;
  }
}
.aw-preview__send-test-form {
  margin: 0;
  float: left;
}
.aw-preview__send-test-form .email-input {
  font-size: 13.5px;
  margin-top: 0;
  padding: 5px 8px 4px;
  vertical-align: top;
  width: 220px;
  margin-right: -6px;
  height: 29px;
}
.aw-preview__send-test-form.aw-loading {
  opacity: 0.4;
}
.aw-preview .from {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 430px;
}

.aw-preview__loader-container {
  background: #ffffff;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.wp-core-ui a.aw-preview-options-button {
  text-decoration: none;
  float: right;
  margin-left: 9px;
}